.ev-paginate {
    display: flex;
    justify-content: center;
    padding: 5px 0;
}
.ev-paginate .pagination {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.ev-paginate .pagination li {
    margin: 0 4px;
    background: #fefeff;
    border: 1px solid #bbbbbb;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
}
.ev-paginate .pagination li.disabled {
    background: #6d6d6d;
    color: #fff;
    border-color: #6d6d6d;
}

.ev-paginate .pagination li.active {
    background: #0196ff;
    color: #fff;
    border-color: #0196ff;
}
.ev-paginate .pagination li a {
    margin: 2px 5px;
    padding: 5px 5px;
    line-height: 2;
}
.ev-paginate .pagination li a:focus {
    outline: none;
}