.month_view_main {
    padding: 15px;
}
.month-heading, .month-week-row {
    grid-template-columns: repeat(auto-fill, 14.28%);
    grid-template-rows: repeat(auto-fill, 100%);
}
.grid-row {
    border-left: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
}
.week-heading, .month-heading, .day-heading{
    border: 0 none;
}
.grid-cell{
    border-right: 1px solid #ccc;
}
.week-heading .grid-cell, .month-heading .grid-cell, .day-heading .grid-cell{
    border-right: 1px solid #aedcf7;
    background: #89cdf5;
    min-height: 35px;
    text-transform: uppercase;
    font-size: 16px;
}

.week-view .event-view {
    width: initial;
}

.day-view .event-view {
    width: initial;
}

.event-view {
    background: #e6e5e5;
    width: 100%;
    border-radius: 3px;
    margin: 0 5px;
    padding: 3px;
    font-size: 16px;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.event-count a{
    color: #333;
    text-decoration: none;
}
.grid-cell.curr-month-cell.today.month_single_day_view .date-view{
    background: #1a73e8;
    color: #fff;
    padding: 5px;
    border-radius: 50%;
}