@import '../../styles/_buttons.css';


button.go-back::after {
    content: '\00AB';
    font-size: 14px;
}

button.go-next::after {
    content: '\00BB';
    font-size: 14px;
}

.top-control-container {
    display: flex;
    align-items: center;
    padding: 15px;
}

.left-control, .right-control {
    display: flex;
    flex-grow: 1;
}

.left-control {
    justify-content: flex-start;
}

.right-control {
    justify-content: flex-end;
}

.status-control {
    display: flex;
    flex-grow: 1;
    justify-content: center;
}