.App {
    font-family: sans-serif;
    text-align: center;
  }

  .grid-cell.curr-month-cell.today.month_single_day_view .date-view {
    background: #fff;
    color: #46de52 !important;
    padding: 5px;
    border-radius: 50%;
  }
  .grid-row {
    border-left: none;
    border-bottom: none;
  }
  .grid-cell {
    border-right: none;
  }
  .week-heading .grid-cell,
  .month-heading .grid-cell,
  .day-heading .grid-cell {
    border-right: none;
    background: none;
    min-height: 35px;
    text-transform: uppercase;
    font-size: 16px;
    color: #46c0de;
  }
  