button.ev-btn {
    padding: 5px 8px;
    background: #d2d2d2;
    border: 0;
    cursor: pointer;
    box-shadow: none;
    outline: none;
    /* box-shadow: 0px 0px 3px #0000009e; */
    height: 30px;
    color: #fff;
}

button.ev-btn:active {
    background: #969494;
}

button.ev-btn.btn-blue {
    background: #26c5f5;
}
button.ev-btn.btn-blue:active {
    background: #1d95b9;
}
button.ev-btn.active {
    color: black;
    background: #dadada;
    cursor: default;
}
button.ev-btn.btn-blue.week{
    margin: 0 1px;
}
button.ev-btn.btn-red {
    background: #f94e46;
}
button.ev-btn.btn-red:active {
    background: #d2271f;
}

button.ev-btn.btn-green {
    background: #50bd43;
}
button.ev-btn.btn-green:active {
    background: #2c8422;
}