@import '../../styles/_buttons.css';

.week-view .month_single_day_view, .day-view .month_single_day_view {
    width: 100%;
}

.month_single_day_view {
    position: relative;
}
.month_single_day_view .date-view {
    position: absolute;
    top: 5px;
    right: 5px;
}
.event-view .event-color {
    width: 15px;
    height: 15px;
    background: #05f3a3;
    display: inline-block;
    border-radius: 50%;
    margin-right: 5px;
}
.event-list-header {
    position: relative;
    padding: 15px;
    background: #29529a;
}
.event-list-header h2 {
    margin: 0;
    display: inline-block;
    color: #fff;
}
.event-list-header .close::after {
    content: '\2715';
    position: absolute;
    top: 5px;
    right: 5px;
    font-size: 25px;
    background: #002b61;
    border-radius: 50%;
    padding: 0 4px;
    cursor: pointer;
    color: #fff;
}

.event-list-body {
    overflow-y: auto;
    max-height: 250px;
}

.event-list-body .ev-body {
    
}
.event-list-body .ev-list {
    list-style: none;
    padding: 0;
    margin: 10px;
    max-width: 400px;
}

.event-list-body .event-list-item {
    padding: 10px 5px;
    margin: 5px 0;
    border: 1px solid #dcdcdc;
    border-radius: 5px;
    cursor: pointer;
}

.event-list-body .event-list-item .event-name {
    display: flex;
}

.event-list-body .event-list-item .event-time {
    display: flex;
    justify-content: flex-end;
    font-size: 12px;
    font-weight: 600;
}

.ev-single .ev-single-title {
    display: flex;
    padding: 15px;
}

.ev-single .ev-single-date {
    display: flex;
    justify-content: flex-end;
    padding: 15px;
    font-size: 15px;
    font-weight: 600;
}
.ev-single .ev-single-control {
    display: flex;
    justify-content: center;
    margin: 10px;
}
.ev-delete, .ev-close {
    margin: 0 5px;
}

.edit-event-form {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.ev-single .edit-event-form .form-line {
    display: flex;
    flex-direction: column;
    padding: 5px 15px;
}
.form-line label {
    padding: 5px 0;
}

.form-line input {
    display: block;
    width: 100%;
    padding: .375rem .2rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.react-datepicker__day.disabled {
    background: #cccccc;
    border-radius: 5px;
    cursor: default;
    user-select: none;
    pointer-events: none;
}

.month_single_day_view.disabled, .event-list-item.disabled {
    cursor: default;
    user-select: none;
    pointer-events: none;
}

.event-list-item.disabled {
    background: #d2d2d2;
}

.event-link-target {
    color: #001fff !important;
    text-decoration: underline !important;
}